import React, { Component } from 'react';
import { connect } from "react-redux";
import axios from 'axios';

import { saveProfile, showHeader } from "../actions/common";

const failSignin = (err) => {
  alert('  .');
}

class MainContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      token: window.location.getParameterByName('access_token')
    };
    this.responseGoogle = this.responseGoogle.bind(this);
    this.getProfile = this.getProfile.bind(this);
  }

  componentDidMount() {
    const {token} = this.state;
    const {showHeaderView} = this.props;
    showHeaderView(false);
    if(token) this.responseGoogle(token);
  }

  componentWillUnmount() {
    const {showHeaderView} = this.props;
    showHeaderView(true);
  }

  async getProfile(response) {
    if(response.status === 200 && response.data.access_token) {
      const { history, saveUserInfo } = this.props;
      window.cookieHandler.set('customtoken', `Token ${response.data.access_token}`)
      try {
        const res = await axios({
          headers: {'Authorization': window.cookieHandler.get('customtoken')},
          url: `${process.env.NODE_ENV === "development" ? "http://127.0.0.1:8000" : "https://awesome-school.net"}/api/v1/users`,
          method: 'GET'
        })
        saveUserInfo(res.data.data);
        history.push('/notice');
      } catch(e) {
        alert('   .');
      }
    } else {
      failSignin();
    }
  }

  async responseGoogle(token) {
    try {
      const res = await axios({
        url: `${process.env.NODE_ENV === "development" ? "http://127.0.0.1:8000" : "https://awesome-school.net"}/rest-auth/google`,
        method: 'POST',
        data: {
         access_token: `${token}`
        }
      })
      this.getProfile(res)
    } catch(e) {
      alert('  .');
    }
  }

  render() {
    return (
      <div className="main-container">
        ...
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
  viewMenu: common.viewMenu
});
const mapDispatchToProps = dispatch => ({
  saveUserInfo: userInfo => dispatch(saveProfile(userInfo)),
  showHeaderView: flag => dispatch(showHeader(flag)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainContainer);
