import React, { Component } from 'react';
import { connect } from "react-redux";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

import SidebarButton from '../components/header/sidebarButton';
import { regionOption, schoolOption, dayOption, programOption, applyOption } from "../utils/static";
import Header from "./HeaderContainer";
import ClassCard from "../components/class";
import HeaderComponent from "../components/myclass/headerComponent";
import api from "../utils/api"
import '../styles/classlist.scss';


class ClassContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      class_list: [],
      region: false,
      school: false,
      day: false,
      program: false,
      applied: false,
      params: window.location.search || false,
      modal: false,
      type: "",
      uuid: "",
    };
    this.getClass = this.getClass.bind(this);
    this.filterData = this.filterData.bind(this);
    this.toggle = this.toggle.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.requestClass = this.requestClass.bind(this);
    this.cancelClass = this.cancelClass.bind(this);
  }

  componentDidMount() {
    this.getClass();
  }

  componentDidUpdate(prevProps, prevState) {
    const { params } = this.state;
    if(prevState.params !== params) this.getClass();
  }

  async getClass() {
    const { params } = this.state;
    const response = await api('get', `/api/v1/mentor/education${params? `?${params}` : ""}`);
    if(!response) return;
    this.setState({class_list: response.data.data});
  }

  toggleModal(type, uuid) {
    const { modal } = this.state;
    if(type ) {
      this.setState({
        type,
        uuid,
        modal: !modal,
      });
    } else {
      this.setState({modal: !modal});
    }
  }

  filterData(params) {
    this.setState({ params });
  }

  toggle(type) {
    this.setState(prevState => ({
      [type]: !prevState[type]
    }));
  }

  async requestClass() {
    const {uuid} = this.state;
    const response = await api('post',`/api/v1/mentor/education/${uuid}/apply`);
    if(!response) return;
    alert(' ');
    window.location.reload();
  }

  async cancelClass() {
    const {uuid} = this.state;
    const response = await api('delete',`/api/v1/mentor/education/${uuid}/cancel`);
    if(!response) return;
    alert(' ');
    window.location.reload();
  }

  render() {
    const {class_list, region, school, day, program, applied, modal, type, params} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <SidebarButton />
          <div className="title">  </div>
        </div>
        <div className="classlist-container child-container">
          <div className={`bg-container ${modal ? 'open' : 'displaynone'}`} onClick={this.toggleModal} />
          <div className={`modal-container ${modal ? 'open' : 'displaynone'}`}>
            <div className="body">
              <div className="title">
                { type === 'request' ? ' ' : ' ' }
              </div>
              <div className="text">
                { type === 'request' ? '' : '' }
                ?
              </div>
            </div>
            <div className="footer">
              <div className="btn cancel" onClick={() => this.toggleModal()}></div>
              <div className="btn confirm" onClick={type === 'request' ? this.requestClass : this.cancelClass}></div>
            </div>
          </div>
          <div className="class-container">
            <table>
              <colgroup>
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
              </colgroup>
              <thead>
                <tr>
                  <th className="region">
                    <HeaderComponent type="region" toggle={this.toggle} dropdownOpen={region} optionList={regionOption} filterData={this.filterData} />
                  </th>
                  <th className="school">
                    <HeaderComponent type="school" toggle={this.toggle} dropdownOpen={school} optionList={schoolOption} filterData={this.filterData} />
                  </th>
                  <th className="day">
                    <HeaderComponent type="day" toggle={this.toggle} dropdownOpen={day} optionList={dayOption} filterData={this.filterData} />
                  </th>
                  <th className="program">
                    <HeaderComponent type="program" toggle={this.toggle} dropdownOpen={program} optionList={programOption} filterData={this.filterData} />
                  </th>
                  <th className="applied">
                    <HeaderComponent type="applied" toggle={this.toggle} dropdownOpen={applied} optionList={applyOption} filterData={this.filterData} />
                  </th>
                </tr>
              </thead>
              <tbody>
                {
                  class_list.length ?
                    class_list.map((item, index) => 
                      <ClassCard key={index} item={item} toggleModal={this.toggleModal} />
                    ) : (
                      <tr>
                        <td colSpan="5" style={{textAlign: 'center'}}>
                          {params ? '  ' : ' '}
                              
                        </td>
                      </tr>
                    )
                }
              </tbody>
            </table>
          </div>
        </div>
      </div>
    )
  }
}

export default ClassContainer;
