import React, { Component } from 'react';
import { connect } from "react-redux";

import { openSidebar } from "../../actions/common";
import sidemenu from '../../images/global/btn-side-menu@2x.png'


class Sidebar extends Component {
  render () {
    const {triggerSidebar} = this.props;
    return (
      <img
        className="sidebar-button left-button"
        src={sidemenu} 
        onClick={triggerSidebar}
      />
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
  isSidebarOpen: common.isSidebarOpen,
});

const mapDispatchToProps = dispatch => ({
  triggerSidebar: () => dispatch(openSidebar()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Sidebar);
