import React, { Component } from 'react';
import axios from "axios";

import Header from "./HeaderContainer";
import SidebarButton from '../components/header/sidebarButton';
import DetailHeader from "../components/detail/header";
import Comment from "../components/comment";
import api from "../utils/api";
import '../styles/write.scss';
import addImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = () => {
  document.getElementById('attachBtn').click();
}

class BoardWriteContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      title: "",
      content: "",
      tag: "",
      images: [],
      files: [],
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.postApi = this.postApi.bind(this);
    this.addImage = this.addImage.bind(this);
    this.removeImage = this.removeImage.bind(this);
    this.goBack = this.goBack.bind(this);
    this.getApi = this.getApi.bind(this);
    this.postImages = this.postImages.bind(this);
  }

  componentDidMount() {
    if(window.location.pathname.includes('modify') && window.location.getParameterByName('id')) this.getApi();
  }

  async getApi() {
    const board_id = window.location.getParameterByName('id');
    const response = await api('get',`/api/v1/community/${board_id}`);
    if(!response) return;
    const {title, content, images, raw_tags} = response.data.data
    this.setState({title, content, images, tag: raw_tags || ""});
  }

  addImage(event) {
    const {images, files} = this.state;
    console.log(images, files)
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      files.push(event.target.files[0])
      reader.onload = (e) => {
        images.push(e.target.result);
        console.log(images, files)
        this.setState({ images, files });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  async postImages(abort_upload, finish_upload, image_urls) {
    const { files } = this.state;
    const board_id = window.location.getParameterByName('id');
    const res = await Promise.all(image_urls.map(async (item, index) => {
      const formData = new FormData();
      formData.append('x-amz-date', item.fields['x-amz-date']);
      formData.append('x-amz-credential', item.fields['x-amz-credential']);
      formData.append('x-amz-algorithm', item.fields['x-amz-algorithm']);
      formData.append('x-amz-signature', item.fields['x-amz-signature']);
      formData.append('key', item.fields.key);
      formData.append('policy', item.fields.policy);
      formData.append('acl', item.fields.acl);
      formData.append('file', files[index]);

      const response = await axios({
        method: 'post',
        url: item.url,
        data: formData,
        withCredentials: false,
      });
      console.log("POST..ing")
      console.log(response);
      return response.status === 204;
    }))
    console.log("POST..end!", res)
    const resultMethod = res.includes(false) ? abort_upload : finish_upload;
    const res2 = await api(resultMethod.method, resultMethod.url);
    console.log("POST..Result : ", res2)
    if(!res.includes(false) && res2.status === 200) {
      alert('  .');
      this.goBack();
    } else {
      alert('    .');
    }
  }



  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  goBack(){
    const {history} = this.props;
    history.push('/board');
  }

  async postApi() {
    const {title, content, tag, images} = this.state;
    const board_id = window.location.getParameterByName('id');
    if(!title) {
      swal(' ');
      return false;
    }
    if(!content) {
      swal(' ');
      return false;
    }
    const postData = {
      title,
      content,
    }
    if(tag) postData.tag = tag;
    if(!board_id) postData.image = images.length

    // if(tag) postData.tag = tag.replace(/ /g,", ");
    const response = await api(board_id ?'put' : 'post',`/api/v1/community${board_id ? `/${board_id}` : ''}`, postData);
    if(!response) return;
    const { abort_upload, finish_upload, image_urls } = response.data;
    if(abort_upload && finish_upload && image_urls) {
      const res = this.postImages(abort_upload, finish_upload, image_urls)
    } else {
      alert('  .');
      this.goBack();
    }
  }

  removeImage(index) {
    const {images, files} = this.state;
    images.splice(index,1);
    files.splice(index,1);
    this.setState({images, files});
  }

  render() {
    const {title, content, tag, image, images} = this.state;
    const board_id = window.location.getParameterByName('id');
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}></div>
          <div className="title"></div>
          <div className={`register-btn ${title && content ? "on" : ""}`} onClick={this.postApi}></div>
        </div>
        <div className="write-container child-container">
          <div className="title content-wrapper">
            <div className="title"></div>
            <input className="inputbox" name="title" onChange={this.changeHandler} value={title} placeholder=" " />
          </div>
          <div className="tag content-wrapper">
            <div className="title">
              <span className="main"></span>
              <span className="sub"> </span>
            </div>
            <input className="inputbox tag" name="tag" onChange={this.changeHandler} value={tag} placeholder="#  (,  )" />
          </div>
          {
            !board_id ? (
              <div className="image content-wrapper">
                <div className="title">
                  <span className="main"> </span>
                  <span className="sub">5M   </span>
                </div>
                <div className="image-wrapper">
                  {
                    images.map((item, index) => (
                      <div className="attach-wrapper" key={index}>
                        <div className="close-btn" onClick={() => this.removeImage(index)}>x</div>
                        <img className="attach-img" src={item} />
                      </div>
                    ))
                  }
                  <div className="image-addbox" style={{backgroundImage: `url("${addImage}")`}} onClick={() => onAttachImg()} />
                </div>
                <input id="attachBtn" className="hide-button" type='file' accept="image/*" onChange={this.addImage} />
              </div>) : null
          }
          <div className="content content-wrapper">
            <div className="title"></div>
            <textarea className="inputbox" name="content" placeholder=" " onChange={this.changeHandler} value={content} rows="8" />
          </div>
        </div>
      </div>
    )
  }
}

export default BoardWriteContainer;
