import React, { Component } from 'react';

import Header from "./HeaderContainer";
import SidebarButton from '../components/header/sidebarButton';
import Card from "../components/notice/card";
import api from "../utils/api";
import {classTime} from "../utils/static";
import '../styles/payment.scss';

const monthList = () => {
  const todayYear = new Date().getFullYear();
  const todayMonth = new Date().getMonth() + 1;
  const startYear = '2012';
  const monthArr = [];
  for (let i=startYear; i <= todayYear; i++) {
    for (let j=1; j<=12;j++) {
      if(i !== todayYear || (i === todayYear && j <= todayMonth)) monthArr.push(`${i}-${(`0${j}`).slice(-2)}`);
    }
  }
  return monthArr;
}

class PaymentContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        total_class_wage: 37500,
        total_extra_expense: 0,
        details: [
          {
            basic_details: {
              class_id: 622,
              date: "2017-08-18",
              day: "Monday",
              start_time: "13:30:00",
              end_time: "16:00:00",
              minus_time: 0,
              inning: 1
            },
            wage_per_inning: 37500,
            transport_cost: 0,
            item_needed: 0,
            food_exp: 0,
            accommodation_exp: 0,
            others: 0,
            school: "00",
          },
          {
            basic_details: {
              class_id: 622,
              date: "2017-08-18",
              day: "Monday",
              start_time: "13:30:00",
              end_time: "16:00:00",
              minus_time: 0,
              inning: 1
            },
            wage_per_inning: 37500,
            transport_cost: 0,
            item_needed: 0,
            food_exp: 0,
            accommodation_exp: 0,
            others: 0,
            school: "00",
          }
        ]
      },
      fromDate: '',
      toDate: '',
      duration: 0,
      feeOption: true,
    };
    this.changeFeeOption = this.changeFeeOption.bind(this);
    this.getData = this.getData.bind(this);
    this.onChangeDate = this.onChangeDate.bind(this);
    this.filterData = this.filterData.bind(this);
  }

  componentDidMount() {
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    const {duration, toDate, fromDate} = this.state;
    if(prevState.duration !== duration || prevState.toDate !== toDate || prevState.fromDate !== fromDate) {
      this.getData();
    }
  }

  onChangeDate(e) {
    this.setState({[e.target.name]: e.target.value})
  }

  async getData() {
    const {duration, toDate, fromDate} = this.state;
    let url = '/api/v1/mentor/my_classes/payments';
    if(duration) {
      url += `?months=${duration}`
    } else if(toDate || fromDate) {
      const nowYear = new Date().getFullYear();
      const nowMonth = new Date().getMonth();
      let fromValue = '';
      let toValue = '';
      if(fromDate) fromValue = new Date(fromDate.split('-')[0], fromDate.split('-')[1] - 1, 1).getTime();
      if(toDate) toValue = new Date(toDate.split('-')[0], toDate.split('-')[1], 1).getTime();
      url += `?from=${fromValue}&to=${toValue}`;
    }

    const response = await api('get', url);
    if(!response) return;
    this.setState({content: response.data});
  }

  changeFeeOption(value) {
    this.setState({feeOption: value});
  }

  searchMonth(month) {
    this.setState({
      duration: month,
      fromDate: "",
      toDate: ""
    })
  }

  filterData() {
    const {fromDate, toDate} = this.state;
    this.setState({duration: 0})
  }

  render() {
    const {content, fromDate, toDate, duration, feeOption} = this.state;
    const mvalue = {year: 2015, month: 11}
    const mrange = {from: {year: 2014, month: 8}, to: {year: 2015, month: 5}}
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <SidebarButton />
          <div className="title"> </div>
        </div>
        <div className="payment-container child-container">
          <div className="search-container">
            <div className="month-option">
              <div className={`month-button month-1 ${ duration === 1 ? "on" : "" }`} onClick={()=>this.searchMonth(1)}> 1</div>
              <div className={`month-button month-3 ${ duration === 3 ? "on" : "" }`} onClick={()=>this.searchMonth(3)}> 3</div>
              <div className={`month-button month-6 ${ duration === 6 ? "on" : "" }`} onClick={()=>this.searchMonth(6)}> 6</div>
            </div>
            <div className="month-range">
              <div className="input-wrapper">
                <select className="date-selector" name="fromDate" onChange={this.onChangeDate} value={fromDate}>
                  <option value="">---- --</option>
                  {
                    monthList().reverse().map((item,index) => {
                      const year = item.split('-')[0];
                      const month = item.split('-')[1];
                      return (<option key={index} value={item}>{`${year} ${month}`}</option>)
                    })
                  }
                </select>
                <span></span>
              </div>
              <div className="input-wrapper">
                <select className="date-selector" name="toDate" onChange={this.onChangeDate} value={toDate}>
                  <option value="">---- --</option>
                  {
                    monthList().reverse().map((item,index) => {
                      if(item<fromDate) return null;
                      const year = item.split('-')[0];
                      const month = item.split('-')[1];
                      return (<option key={index} value={item}>{`${year} ${month}`}</option>)
                    })
                  }
                </select>
                <span></span>
              </div>
              <div className="search-button" onClick={this.filterData} />
            </div>
          </div>
          <div className="payment-wrapper">
            <div className="detail-header">
              <span></span>
              <div className="detail-subheader">
                <span className="from-date">2018.04</span>
                {' - '}
                <span className="to-date">2018.05</span>
              </div>
            </div>
            <div className="summary-wrapper">
              <table>
                <tbody>
                  <tr>
                    <td className="title"></td>
                    <td className="value">
                      {(content.total_class_wage).formatMoney()}
                      
                    </td>
                  </tr>
                  <tr>
                    <td className="title"></td>
                    <td className="value">
                      {(content.total_extra_expense).formatMoney()}
                      
                    </td>
                  </tr>
                  <tr className="total">
                    <td className="title"></td>
                    <td className="value">
                      {` `}
                      {(content.total_class_wage + content.total_extra_expense).formatMoney()}
                      
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

            <div className="detail-header"> </div>
            <div className="select-wrapper">
              <div className="select-header">
                <div className={`fee-button main-fee ${feeOption ? "on" : ""}`} onClick={()=>this.changeFeeOption(true)}> </div>
                <div className={`fee-button etc-fee ${!feeOption ? "on" : ""}`} onClick={()=>this.changeFeeOption(false)}> </div>
              </div>
              {
                content.details.map((item, index) => (
                  <div className="fee-wrapper" key={index}>
                    <div className="inning-info">
                      <span>{`${item.basic_details.school} : `}</span>
                      <span> 
                        {'#'}
                        {item.basic_details.inning}
                        
                      </span>
                      <span>
                        {' ('}
                        {item.basic_details.date}
                        )
                      </span>
                    </div>
                    <div className="fee-info">
                      <table>
                        <colgroup>
                          <col width="25%" />
                          <col width="25%" />
                          <col width="25%" />
                          <col width="25%" />
                        </colgroup>
                        {
                          feeOption ? (
                            <tbody>
                              <tr>
                                <td className="title"></td>
                                <td className="value">{classTime(item.basic_details.start_time)}</td>
                                <td className="title"></td>
                                <td className="value">
                                  {item.basic_details.minus_time}
                                  
                                </td>
                              </tr>
                              <tr>
                                <td className="title"></td>
                                <td className="value">{classTime(item.basic_details.end_time)}</td>
                                <td className="title"></td>
                                <td className="value">
                                  {(item.wage_per_inning).formatMoney()}
                                  
                                </td>
                              </tr> 
                            </tbody>) : (
                              <tbody>
                                <tr>
                                  <td className="title"></td>
                                  <td className="value">
                                    {(item.transport_cost).formatMoney()}
                                    
                                  </td>
                                  <td className="title"></td>
                                  <td className="value">
                                    {(item.food_exp).formatMoney()}
                                    
                                  </td>
                                </tr>
                                <tr>
                                  <td className="title"></td>
                                  <td className="value">
                                    {(item.item_needed).formatMoney()}
                                    
                                  </td>
                                  <td className="title">
                                    
                                    <p className="sub">(,)</p>
                                  </td>
                                  <td className="value">
                                    {(item.others).formatMoney()}
                                    
                                  </td>
                                </tr>
                                <tr>
                                  <td className="title"></td>
                                  <td className="value">
                                    {(item.accommodation_exp).formatMoney()}
                                    
                                  </td>
                                  <td />
                                  <td />
                                </tr>
                              </tbody>)
                        }
                      </table>
                    </div>
                  </div>
                ))
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default PaymentContainer;
