import createActions from "../utils/createAction";

export const CHANGE_TITLE = "CHANGE_TITLE";
export const OPEN_SIDEBAR = "OPEN_SIDEBAR";
export const VIEW_MENU  = "VIEW_MENU";
export const SAVE_PROFILE = "SAVE_PROFILE";
export const SHOW_HEADER = "SHOW_HEADER";

export const changeTitle = title => createActions(CHANGE_TITLE, title);
export const openSidebar = () => createActions(OPEN_SIDEBAR);
export const viewSidemenu = view => createActions(VIEW_MENU, view);
export const saveProfile = info => createActions(SAVE_PROFILE, info);
export const showHeader = flag => createActions(SHOW_HEADER, flag);
