import React, { Component } from 'react';
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';

const horizontalLabels = {
  0: '0',
  100: '10',
}

class SatisfactionContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      value: 10,
      class_id: window.location.getParameterByName('id'),
    };
    this.postApi = this.postApi.bind(this);
    this.goBack = this.goBack.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    // const response = await api('get','/api/v1/community');
    // if(!response) return;
    // this.setState({boards: response.data.data});
  }

  async postApi() {
    const {class_id, value} = this.state;
    const { history } = this.props;
    const postData = {
      index: value
    }
    const response = await api('post', `/api/v1/mentor/my_classes/${class_id}/satisfaction`, postData);
    if(!response) return;
    alert('  ');
    history.push(`/myclass/detail?id=${class_id}`);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  handleChange(value) {
    this.setState({value: Number(value.toFixed(1))})
  };

  render() {
    const {boards, title, content, value} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">  </div>
          <div className="register-btn on" onClick={this.postApi}></div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">  </div>
            <div className='point'>
              <span className="num">{value}</span>
              <span className="unit"></span>
            </div>
            <Slider
              min={0}
              max={10}
              step={0.1}
              value={value}
              labels={horizontalLabels}
              onChange={this.handleChange}
            />
          </div>
        </div>
      </div>
    )
  }
}

export default SatisfactionContainer;
