import React, { Component } from 'react';
import Swiper from 'react-id-swiper';

import SidebarButton from '../components/header/sidebarButton';
import Header from "./HeaderContainer";
import DetailHeader from "../components/detail/header"
import Comment from "../components/comment"
import api from "../utils/api"
import '../styles/notice.scss';
import backImg from '../images/global/btn-back@2x.png';

const swiperParams = {
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  },
  spaceBetween: 30,
  shouldSwiperUpdate: true,
}

class NoticeDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        content: "",
        created_at: "",
        images: [],
        pinned: false,
        title: "",
        written_by: "",
        comments: []
      },
      comment: "",
      commentId: false,
      notice_id: window.location.getParameterByName('id'),
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.submitComment = this.submitComment.bind(this);
    this.getApi = this.getApi.bind(this);
    this.goBack = this.goBack.bind(this);
    this.onWriteComment = this.onWriteComment.bind(this);
    this.onModifyComment = this.onModifyComment.bind(this);
    this.deleteComment = this.deleteComment.bind(this);
  }

  componentDidMount() {
    this.getApi();
  }

  onModifyComment(id) {
    const {content} = this.state
    const selectedComment = content.comments.filter(item => item.id === id)[0];
    this.setState({ 
      commentId: id,
      comment: selectedComment.content,
    });
  }

  onWriteComment() {
    this.setState({ 
      commentId: false,
      comment: ''
    });
  }

  async getApi() {
    const {notice_id} = this.state;
    const response = await api('get',`/api/v1/notice/${notice_id}`);
    if(!response) return;
    this.setState({
      content: response.data.data,
      comment: "",
    });
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  async submitComment() {
    const {comment, notice_id, commentId} = this.state;
    const postData = {
      content: comment,
    }
    if(commentId) postData.comment_id = commentId;
    const response = await api(commentId ? 'put' : 'post', `/api/v1/notice/${notice_id}/comments`, postData);
    if(!response) return;
    swal(' ');
    this.getApi();
  }

  async deleteComment(id) {
    const {notice_id} = this.state;
    const postData = {
      comment_id: id
    }
    const response = await api('delete', `/api/v1/notice/${notice_id}/comments`, postData);
    if(!response) return;
    swal(' ');
    this.getApi();
  }

  render() {
    const {content, comment, commentId} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title"></div>
        </div>
        <div className="notice-container child-container">
          <div className="detail-container">
            <DetailHeader board={content} />
            {
              content.images.length ? (
                <Swiper {...swiperParams}>
                  {
                    content.images.map((item, index) => 
                      <div key={index} style={{backgroundImage: `url("${item}")`}} />
                      // <img key={index} src={item.image} />
                    )
                  }
                </Swiper>
              ) : null
            }
            <div className="detail-content" dangerouslySetInnerHTML={{ __html: content.content }} />
            <div className="comment-container">
              <div className="title">
                <span> </span>
                {content.comments.length}
              </div>
              <div className="comment-body">
                <table>
                  <colgroup>
                    <col width='56' />
                    <col width='auto' />
                  </colgroup>
                  <tbody>
                    {
                      content.comments.map((item, index) => (
                        <Comment
                          key={index}
                          comment={item}
                          onModifyComment={this.onModifyComment}
                          deleteComment={this.deleteComment}
                        />
                      ))
                    }
                  </tbody>
                </table>
              </div>
            </div>
            <div className="comment-inputbox">
              <input className={`comment-input ${commentId ? 'modify' : ''}`} type="text" placeholder="" name="comment" onChange={this.changeHandler} value={comment} />
              <button className="register-button" type="button" onClick={this.submitComment}>{commentId ? '' : ''}</button>
              {
                commentId ? (
                  <button className="cancel-button" type="button" onClick={this.onWriteComment}></button>   
                ) : null
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default NoticeDetailContainer;

