import axios from "axios";
/* eslint no-use-before-define: "off", no-underscore-dangle: "off" */

const instance = axios.create({
  baseURL: process.env.NODE_ENV === "development" ? "https://awesome-school.net" 
  : "https://awesome-school.net",
  withCredentials: false,
  // validateStatus: status => (status >= 200 && status < 600)
});

const api = async (method, url, data) => {
  try {
    const response = await instance({
      headers: {'Authorization': window.cookieHandler.get('customtoken')},
      method,
      url,
      data,
    });
    return response;
  } catch(e) {
    const apiMethod = method.toLowerCase();
    if(apiMethod === 'get') {
      alert(`[ : ${e.response && e.response.status ? e.response.status : '500'}]    .`);
    } else if(apiMethod === 'delete') {
      alert(`[ : ${e.response && e.response.status ? e.response.status : '500'}]    .`);
    } else {
      alert(`[ : ${e.response && e.response.status ? e.response.status : '500'}]    .`);
    }
    return false;
  }
};

export default api;
