import React, { Component } from 'react';

import Header from "./HeaderContainer";
import SidebarButton from '../components/header/sidebarButton';
import ClassCard from "../components/myclass";
import HeaderComponent from "../components/myclass/headerComponent";
import api from "../utils/api";
import { regionOption, schoolOption, dayOption, programOption, statusOption } from "../utils/static";
import '../styles/classlist.scss';


class MyclassContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      class_list: [],
      region: false,
      school: false,
      day: false,
      program: false,
      status: false,
      params: window.location.search || false,
    };
    this.getClass = this.getClass.bind(this);
    this.toggle = this.toggle.bind(this);
    this.filterData = this.filterData.bind(this);
  }

  componentDidMount() {
    this.getClass();
  }

  componentDidUpdate(prevProps, prevState) {
    const { params } = this.state;
    if(prevState.params !== params) this.getClass();
  }

  async getClass() {
    const { params } = this.state;
    const response = await api('get', `/api/v1/mentor/my_classes${params? `?${params}` : ""}`);
    if(!response) return;
    this.setState({class_list: response.data.data});
  }

  filterData(params) {
    this.setState({ params });
  }

  toggle(type) {
    this.setState(prevState => ({
      [type]: !prevState[type]
    }));
  }

  render() {
    const {class_list, region, school, day, program, status} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <SidebarButton />
          <div className="title"> </div>
        </div>
        <div className="classlist-container child-container">
          <div className="class-container">
            <table>
              <colgroup>
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
              </colgroup>
              <thead>
                <tr>
                  <th className="region">
                    <HeaderComponent type="region" toggle={this.toggle} dropdownOpen={region} optionList={regionOption} filterData={this.filterData} />
                  </th>
                  <th className="school">
                    <HeaderComponent type="school" toggle={this.toggle} dropdownOpen={school} optionList={schoolOption} filterData={this.filterData} />
                  </th>
                  <th className="day">
                    <HeaderComponent type="day" toggle={this.toggle} dropdownOpen={day} optionList={dayOption} filterData={this.filterData} />
                  </th>
                  <th className="program">
                    <HeaderComponent type="program" toggle={this.toggle} dropdownOpen={program} optionList={programOption} filterData={this.filterData} />
                  </th>
                  <th className="status">
                    <HeaderComponent type="status" toggle={this.toggle} dropdownOpen={status} optionList={statusOption} filterData={this.filterData} />
                  </th>
                </tr>
              </thead>
              <tbody>
                {
                  class_list.length ? 
                    class_list.map((item, index) => 
                      <ClassCard key={index} item={item} />
                    )
                  : <tr><td colSpan="5" style={{textAlign: 'center'}}>   </td></tr>
                }
              </tbody>
            </table>
          </div>
        </div>
      </div>
    )
  }
}

export default MyclassContainer;
