import React from "react";
import { Link } from 'react-router-dom'

const Card = ({notice}) => (
  <Link to={`/notice/detail?id=${notice.id}`}>
    <div className="board-card">
      <div className="board-tag">{notice.category}</div>
      <div className="board-title">{notice.title}</div>
      <div className="board-info">
        <div className="board-writer">{notice.written_by || ""}</div>
        <div className="write-date">{notice.created_at.split(" ")[0].replace(/-/g, '/')}</div>
        <div className="reply-count">
           
          {` ${notice.total_comments}`}
        </div>
      </div>
    </div>
  </Link>
)

export default Card
