import React, { Component } from 'react';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import InfiniteScroll from 'react-infinite-scroller';

import { changeTitle } from "../actions/common";
import SidebarButton from '../components/header/sidebarButton';
import Header from "./HeaderContainer";
import Card from "../components/board/card"
import api from "../utils/api"
import '../styles/board.scss';

class BoardContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      boards: [],
      nextUrl: false,
    };
    this.getMoreNotice = this.getMoreNotice.bind(this);
  }

  async componentDidMount() {
    const response = await api('get','/api/v1/community');
    if(!response) return;
    this.setState({
      boards: response.data.data,
      nextUrl: response.data.next_url || false,      
    });
  }

  async getMoreNotice() {
    const {nextUrl, boards} = this.state;
    if(!nextUrl) return;
    const response = await api('get', nextUrl);
    if(!response) return;
    const updatedboards = boards.concat(response.data.data);
    this.setState({
      boards: updatedboards,
      nextUrl: response.data.next_url || false, 
    });
  }

  render() {
    const {boards, nextUrl} = this.state;
    const loader = <div className="loader" key={0}> ...</div>;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <SidebarButton />
          <div className="title"></div>
          <Link to="/board/write">
            <div className="register-btn on"></div>
          </Link>
        </div>
        <InfiniteScroll
          pageStart={0}
          loadMore={this.getMoreNotice}
          hasMore={!!nextUrl}
          loader={loader}
          useWindow={false}
        >
          <div className="board-container child-container">
            <div className="board-list">
              {
                boards.map((board, index) =>
                  <Card key={index} board={board} />
                )
              }
            </div>
          </div>
        </InfiniteScroll>
      </div>
    )
  }
}

export default BoardContainer;
