import React, { Component } from 'react';
import InfiniteScroll from 'react-infinite-scroller';

import Header from "./HeaderContainer";
import SidebarButton from '../components/header/sidebarButton';
import { categoryOption } from "../utils/static";
import Card from "../components/notice/card";
import api from "../utils/api";
import HeaderComponent from "../components/myclass/headerComponent";
import '../styles/notice.scss';
import searchImg from "../images/global/btn-search@2x.png";

class NoticeContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      pinned: [],
      unpinned: [],
      showSearch: false,
      category: false,
      params: '',
      keyword: '',
      searchTitle: '',
      nextUrl: false,
    }
    this.toggle = this.toggle.bind(this);
    this.showSearch = this.showSearch.bind(this);
    this.filterData = this.filterData.bind(this);
    this.changeHandler = this.changeHandler.bind(this);
    this.getMoreNotice = this.getMoreNotice.bind(this);
  }

  componentDidMount() {
    this.getNoticeList();
  }

  componentDidUpdate(prevProps, prevState) {
    const {keyword, searchTitle} = this.state;
    if(prevState.keyword !== keyword || prevState.searchTitle !== searchTitle) this.getNoticeList();
  }

  async getNoticeList() {
    const {showSearch, params, keyword} = this.state;
    let url = '/api/v1/notice';
    if(showSearch && (keyword || params)) url += `?${params || 'category=1'}&search=${keyword}`
    const response = await api('get', url);
    if(!response) return;
    this.setState({
      pinned: response.data.pinned,
      unpinned: response.data.unpinned,
      nextUrl: response.data.next_url || false,
    });
  }

  async getMoreNotice() {
    const {nextUrl, unpinned} = this.state;
    if(!nextUrl) return;
    const response = await api('get', nextUrl);
    if(!response) return;
    const updatedUnpinned = unpinned.concat(response.data.unpinned);
    this.setState({
      unpinned: updatedUnpinned,
      nextUrl: response.data.next_url || false, 
    });
  }

  showSearch() {
    const {showSearch} = this.state;
    this.setState({showSearch: !showSearch});
  }

  filterData(params, searchTitle) {
    this.setState({ params, searchTitle });
  }

  toggle(type) {
    this.setState(prevState => ({
      [type]: !prevState[type]
    }));
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  render() {
    const {pinned, unpinned, showSearch, category, params, keyword, searchTitle, nextUrl} = this.state;
    const loader = <div className="loader" key={0}> ...</div>;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <SidebarButton />
          <div className="title"></div>
          {
            showSearch ? (
              <div className="search-wrapper">
                <HeaderComponent
                  type="category"
                  params={params}
                  toggle={this.toggle}
                  dropdownOpen={category}
                  optionList={categoryOption}
                  filterData={this.filterData}
                  searchTitle={searchTitle}
                />
                <input
                  className="search-input"
                  type="text"
                  placeholder=" "
                  name="keyword"
                  onChange={this.changeHandler}
                  value={keyword}
                />
              </div>
            ) : null
          }
          <div className="register-btn on" onClick={this.showSearch}>
            {
              showSearch ? "" : 
              <img className="search-img" src={searchImg} />
            }
          </div>
        </div>
        <InfiniteScroll
          className="infinite-wrapper"
          pageStart={0}
          loadMore={this.getMoreNotice}
          hasMore={!!nextUrl}
          loader={loader}
          useWindow={false}
        >
          <div className="notice-container child-container list">
            <div className="notice-list">
              <div className="fixed-container">
                {
                  pinned.map((notice, index) =>
                    <Card key={index} notice={notice} />
                  )
                }
              </div>
              <div className="normal-container">
                {
                  unpinned.length ?
                    unpinned.map((notice, index) =>
                      <Card key={index} notice={notice} />
                    ) : 
                    <div className="empty-result"> .</div>
                }
              </div>
            </div>
          </div>
        </InfiniteScroll>
      </div>
    )
  }
}

export default NoticeContainer;
