import { call, put, take, fork, race, select } from "redux-saga/effects";
import * as Common from "../actions/common";

function* titleChange(title) {
  while (true) {
    const request = yield take(Common.CHANGE_TITLE);
    try {
      yield call(Common.changeTitle, title);
    } catch (error) {
      console.log(error);
    }
  }
}

function* viewSidemenu(view) {
	while (true) {
    const request = yield take(Common.VIEW_MENU);
    try {
      yield call(Common.VIEW_MENU, view);
    } catch (error) {
      console.log(error);
    }
  }
}

function* saveProfile(info) {
  while (true) {
    const request = yield take(Common.SAVE_PROFILE);
    try {
      yield call(Common.SAVE_PROFILE, info);
    } catch (error) {
      console.log(error);
    }
  }
}

function* openSidebar() {
  while (true) {
    const request = yield take(Common.OPEN_SIDEBAR);
    try {
      yield call(Common.OPEN_SIDEBAR);
    } catch (error) {
      console.log(error);
    }
  }
}

export default {titleChange, viewSidemenu, saveProfile, openSidebar};
