import React, { Component } from 'react';
import { connect } from "react-redux";
import GoogleLogin from 'react-google-login';
import axios from 'axios';

import logoImg from "../images/global/logo.png";
import { saveProfile, showHeader } from "../actions/common";
import '../styles/main.scss';

class MainContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {};
    this.responseGoogle = this.responseGoogle.bind(this);
    this.getProfile = this.getProfile.bind(this);
    this.failSignin = this.failSignin.bind(this);
  }

  componentDidMount() {
    const {showHeaderView} = this.props;
    showHeaderView(false);
  }

  componentWillUnmount() {
    const {showHeaderView} = this.props;
    showHeaderView(true);
  }

  async getProfile(response) {
    if(response.status === 200 && response.data.access_token) {
      const { history, saveUserInfo } = this.props;
      window.cookieHandler.set('customtoken', `Token ${response.data.access_token}`)
      try {
        const res = await axios({
          headers: {'Authorization': window.cookieHandler.get('customtoken')},
          url: `${process.env.NODE_ENV === "development" ? "https://awesome-school.net" : "https://awesome-school.net"}/api/v1/users`,
          method: 'GET'
        })
        this.setState({log: JSON.stringify(res)})
        // saveUserInfo(res.data.data);
        // history.push('/notice');
      } catch(e) {
        alert('   .');
      }
    } else {
      this.failSignin();
    }
  }

  failSignin(response) {
    this.setState({log: JSON.stringify(response)})
    alert('  .');
  }

  async responseGoogle(response) {
    try {
      const res = await axios({
        url: `${process.env.NODE_ENV === "development" ? "https://awesome-school.net" : "https://awesome-school.net"}/rest-auth/google`,
        method: 'POST',
        data: {
         access_token: `${response.accessToken}`
        }
      })
      this.getProfile(res)
    } catch(e) {
      this.setState({log: JSON.stringify(e)})
      alert('  .');
    }
  }

  render() {
    const {log} = this.state;
    return (
      <div className="main-container">
        <GoogleLogin
          className="google-login"
          clientId="407625889267-170lt8oq7h7me2je4gu9drs85jeran8e.apps.googleusercontent.com"
          buttonText="Google  "
          onSuccess={this.responseGoogle}
          onFailure={this.failSignin}
        />
        <div style={{paddingTop: '15vh',}}>
          <div></div>
          <div>{log}</div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
  viewMenu: common.viewMenu
});
const mapDispatchToProps = dispatch => ({
  saveUserInfo: userInfo => dispatch(saveProfile(userInfo)),
  showHeaderView: flag => dispatch(showHeader(flag)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainContainer);
