import * as ActionTypes from "../actions/common";

const initialState = {
  title: "",
  isSidebarOpen: false,
  viewMenu: true,
  userInfo: {
    name: '',
    nickname: '',
    profile: '',
  },
  showHeader: true,
};

export default (state = initialState, action) => {
  const data = action.payload;
  switch (action.type) {
    case ActionTypes.CHANGE_TITLE:
      return { ...state, title: data };
    case ActionTypes.OPEN_SIDEBAR:
     return { ...state, isSidebarOpen: !state.isSidebarOpen };
    case ActionTypes.VIEW_MENU:
      return { ...state, viewMenu: data}
    case ActionTypes.SAVE_PROFILE:
      return { ...state, userInfo: data}
    case ActionTypes.SHOW_HEADER:
      return { ...state, showHeader: data}
    default:
      return state;
  }
};