window.cookieHandler = {
  set(cname, cvalue, exdate) {
    const d = new Date(exdate);
    if (!exdate) d.setTime(d.getTime() + 365 * 24 * 60 * 60 * 1000);
    const expires = `expires=${d.toUTCString()}`;
    document.cookie = `${cname}=${encodeURI(cvalue)};${expires};path=/`;
    return true;
  },
  get(c_name) {
    if (document.cookie.length > 0) {
      let c_start = document.cookie.indexOf(`${c_name}=`);
      let c_end = false;
      if (c_start !== -1) {
        c_start = c_start + c_name.length + 1;
        c_end = document.cookie.indexOf(";", c_start);
        if (c_end === -1) c_end = document.cookie.length;
        return decodeURI(document.cookie.substring(c_start, c_end));
      }
    }
  },
  remove(name, path, domain) {
    if (this.get(name)) {
      document.cookie = `${name}=${path ? `;path=${path}` : ""}${
        domain ? `;domain=${domain}` : ""
      };expires=Thu, 01 Jan 1970 00:00:01 GMT`;
    }
  },
  checkHttpOnlyCookie(name) {
    const d = new Date();
    d.setTime(d.getTime() + 1000);
    const expires = `expires=${d.toUTCString()}`;

    document.cookie = `${name}=new_value;path=/;${expires}`;
    if (document.cookie.indexOf(`${name}=`) === -1) {
      return true;
    }
    return false;
  }
};

window.location.getParameterByName = function(name, url) {
  if (!url) url = window.location.href;
  name = name.replace(/[[\]]/g, "\\$&");
  const regex = new RegExp(`[?&]${name}(=([^&#]*)|&|#|$)`);
  const results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2]);
};

