window.formatMoney = function(value) {
  let n = value.toString();
  let minusFlag = false;
  if (n.indexOf("-") > -1) {
    [, n] = n.split("-");
    minusFlag = true;
  }
  let result = "";
  for (let i = n.length - 1, j = 0; i >= 0; i--, j++) {
    if (j !== 0 && j % 3 === 0) result = `,${result}`;
    result = n[i] + result;
  }
  return minusFlag ? `-${result}` : result;
};

const documentReady = function() {
  const storageCheck = function() {
    const test = "__localstoragetest__";
    try {
      window.localStorage.setItem("test", test);
      window.localStorage.removeItem("test");
      return;
    } catch (e) {
      alert(
        "      .  ."
      );
    }
  };

  const addEvent = function(object, type, callback) {
    if (object == null || typeof object === "undefined") return;
    if (object.addEventListener) {
      object.addEventListener(type, callback, false);
    } else if (object.attachEvent) {
      object.attachEvent(`on${type}`, callback);
    } else {
      object[`on${type}`] = callback;
    }
  };

  storageCheck();
};

/*
 *   : jQuery $(document).ready(fucntion(){...})  javascript 
 *        jQuery dependency             JS 
 */

// Mozilla, Opera, Webkit
if (document.addEventListener) {
  document.addEventListener(
    "DOMContentLoaded",
    () => {
      // document.removeEventListener("DOMContentLoaded", arguments.callee, false);
      documentReady();
    },
    false
  );
} else if (document.attachEvent) {
  // Internet Explorer
  document.attachEvent("onreadystatechange", () => {
    if (document.readyState === "complete") {
      // document.detachEvent("onreadystatechange", arguments.callee);
      documentReady();
    }
  });
}

function addHours (time, h) {
  const originTime = new Date(time);
  const convertTime = originTime.setHours(originTime.getHours() + h);
  return convertTime;
}
