import React, { Component } from 'react';
import { connect } from "react-redux";

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';

class CommentsContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      classname: '',
      class_id: window.location.getParameterByName('id'),
    };
    this.postApi = this.postApi.bind(this);
    this.goBack = this.goBack.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    // const response = await api('get','/api/v1/community');
    // if(!response) return;
    // this.setState({boards: response.data.data});
  }

  async postApi() {
    const {class_id, classname} = this.state;
    const { history } = this.props;
    if(!classname) {
      alert(' ');
      return false;
    } 
    if(!Number.isInteger(Number(classname))) {
      alert('   (, 3)');
      return false;
    }
    const postData = {
      class_num: classname,
    }
    const response = await api('post', `/api/v1/mentor/my_classes/${class_id}/class_num`, postData);
    if(!response) return;
    alert('  ');
    history.push(`/myclass/detail?id=${class_id}`);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  handleChange(e) {
    this.setState({[e.target.name]: e.target.value})
  };

  render() {
    const {classname} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title"> </div>
          <div className={`register-btn ${classname ? 'on' : ''}`} onClick={this.postApi}></div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="input-title"> </div>
            <input className="input class" type="text" name="classname" placeholder="  . (: 1, 10 )" onChange={this.handleChange} />
          </div>
        </div>
      </div>
    )
  }
}

export default CommentsContainer;
