import React from "react";
import { Link } from 'react-router-dom'

function convertDate(dates) {
  const month = new Date(dates).getMonth()+1;
  const date = new Date(dates).getDate();
  const day = new Date(dates).getDay();
  let convertDay = ''
  switch(day) {
    case 1:
      convertDay = '';
      break;
    case 2:
      convertDay = '';
      break;
    case 3:
      convertDay = '';
      break;
    case 4:
      convertDay = '';
      break;
    case 5:
      convertDay = '';
      break;
    case 6:
      convertDay = '';
      break;
    default:
      convertDay = '';
      break;
  }
  return `${month}/${date} (${convertDay})`;
}

const Card = ({item}) => {
  const status = item.status === "" ? 1 : (item.status === "" ? 2 : 3);
  return(
    <tr>
      <td>
        <Link to={`/myclass/detail?id=${item.id}&status=${status}`}>{item.region}</Link>
      </td>
      <td>
        <Link to={`/myclass/detail?id=${item.id}&status=${status}`}>
          { item.school.includes('') ? item.school.split('')[0] : item.school.split('')[0]}
          { !item.feedback_uploaded ? <span className="no-uploaded"></span> : null}
        </Link>
      </td>
      <td>
        <Link to={`/myclass/detail?id=${item.id}&status=${status}`}>
          {convertDate(item.date)}
        </Link>
      </td>
      <td>
        <Link to={`/myclass/detail?id=${item.id}&status=${status}`}>{item.program}</Link>
      </td>
      <td>
        <Link to={`/myclass/detail?id=${item.id}&status=${status}`}>{item.status}</Link>
      </td>
    </tr>
  )
}

export default Card
