import React from "react";
import { Link } from 'react-router-dom'

const Card = ({board}) => (
  <Link to={`/board/detail?id=${board.id}`} className="board-card-link">
    <div className="board-card">
      <div className="board-title">{board.title}</div>
      <div className="board-info">
        <div className="board-writer">{board.written_by}</div>
        <div className="write-date">{board.created_at.split(" ")[0].replace(/-/g, '/')}</div>
        <div className="reply-count">
           
          {` ${board.total_comments}`}
        </div>
      </div>
    </div>
  </Link>
)

export default Card;
