import React from "react";

const Comment = ({comment, onModifyComment, deleteComment}) => (
  <tr className="comment-card">
    <td className="comment-td">
      <div className="writer-img" style={{backgroundUrl: `url('${comment.mentor_img}')`}} />
    </td>
    <td className="comment-td">
      <div className="comment-content">{comment.content}</div>
      <div className="comment-info">
        <div className="comment-writer">{comment.mentor_name || ""}</div>
        {
          comment.created_at ? 
            <div className="write-date">{comment.created_at.split(' ')[0].replace(/-/g, '/')}</div> : null
        }
      </div>
      {
        comment.written_by_me ? (
          <div className="btn-wrapper">
            <div className="btn" onClick={() => onModifyComment(comment.id)}></div>
            <div className="btn" onClick={() => deleteComment(comment.id)}></div>
          </div>) : null
      }
    </td>
  </tr>
)

export default Comment
