import React, { Component } from 'react';
import { connect } from "react-redux";
import {Link} from "react-router-dom";

import { openSidebar } from "../../actions/common";
import '../../styles/sidebar.scss';
import NoPic from '../../images/global/no-pic.jpg';

const menu = [
    {
      image: `${process.env.PUBLIC_URL}/static/image/global/ic-side-notice-nor@2x.png`,
      image_on: `${process.env.PUBLIC_URL}/static/image/global/ic-side-notice-sel@2x.png`,
      title: '',
      link: '/notice',
    },
    {
      image: `${process.env.PUBLIC_URL}/static/image/global/ic-side-board-nor@2x.png`,
      image_on: `${process.env.PUBLIC_URL}/static/image/global/ic-side-board-sel@2x.png`,
      title: '',
      link: '/board',
    },
    {
      image: `${process.env.PUBLIC_URL}/static/image/global/ic-side-search-nor@2x.png`,
      image_on: `${process.env.PUBLIC_URL}/static/image/global/ic-side-search-sel@2x.png`,
      title: '  ',
      link: '/class',
    },
    {
      image: `${process.env.PUBLIC_URL}/static/image/global/ic-side-class-nor@2x.png`,
      image_on: `${process.env.PUBLIC_URL}/static/image/global/ic-side-class-sel@2x.png`,
      title: ' ',
      link: '/myclass',
    },
    {
      image: `${process.env.PUBLIC_URL}/static/image/global/ic-side-payment-nor@2x.png`,
      image_on: `${process.env.PUBLIC_URL}/static/image/global/ic-side-payment-sel@2x.png`,
      title: '  ',
      link: '/payment',
    },
  ];

class Sidebar extends Component {
  constructor(props) {
    super(props);
  
    this.state = {};
  }

  createLinkView() {
    const {triggerSidebar, location} = this.props;
    return menu.map(item => (
      <Link
        className={`sidebar-link ${location.pathname.includes(item.link) ? 'on' : ''}`}
        key={item.title}
        to={item.link}
        onClick={() => triggerSidebar()}
      >
        <div className="sidebar-button-wrapper" style={{ backgroundImage: `url("${location.pathname.includes(item.link) ? item.image_on : item.image}")`}}>
          <span className="sidebar-link-title">{item.title}</span>
        </div>
      </Link>
    ))
  }
  
  render() {
    const { profile } = this.props;
    return (
      <div className="sidebar-container">
        <div className="userinfo-container">
          <div className="user-image" style={{backgroundImage: `url('${profile.profile || NoPic}')`}} />
          <div className="text-container">
            <div className="user-nickname">{profile.nickname || "No nickname"}</div>
            <div className="user-realname">{profile.name || "No name"}</div>
          </div>
        </div>
        <div className="sidebar-content">
          {this.createLinkView()}
        </div>
      </div>
    )
  }
};

const mapStateToProps = ({ common, router }) => ({
  profile: common.userInfo,
  location: router.location,
});
const mapDispatchToProps = dispatch => ({
  triggerSidebar: () => dispatch(openSidebar()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Sidebar);
