import React, { Component } from 'react';
import axios from "axios";
import Header from "./HeaderContainer";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';
import addPlusImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = () => {
  document.getElementById('attachBtn').click();
}

class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      image: false,
      images: [],
      class_id: window.location.getParameterByName('id'),
    };
    this.goBack = this.goBack.bind(this);
    this.addImage = this.addImage.bind(this);
    this.getUploadUrl = this.getUploadUrl.bind(this);
    this.postImages = this.postImages.bind(this);
  }

  async getUploadUrl() {
    const {images, class_id} = this.state;
    const { history } = this.props;
    if(!images.length) {
      alert('   ');
      return false;
    }
    const postData = {
      image: images.length
    }
    const response = await api('post', `/api/v1/mentor/my_classes/${class_id}/images`, postData);
    if(!response) return;
    const { image_urls } = response.data;
    const res = this.postImages(image_urls);
  }

  async postImages(image_urls) {
    const { images } = this.state;
    const board_id = window.location.getParameterByName('id');
    const res = await Promise.all(image_urls.map(async (item, index) => {
      const formData = new FormData();
      formData.append('x-amz-date', item.fields['x-amz-date']);
      formData.append('x-amz-credential', item.fields['x-amz-credential']);
      formData.append('x-amz-algorithm', item.fields['x-amz-algorithm']);
      formData.append('x-amz-signature', item.fields['x-amz-signature']);
      formData.append('key', item.fields.key);
      formData.append('policy', item.fields.policy);
      formData.append('acl', item.fields.acl);
      formData.append('file', images[index]);

      const response = await axios({
        method: 'post',
        url: item.url,
        data: formData,
        withCredentials: false
      });
      console.log("POST..ing")
      console.log(response);
      return response.status === 204;
    }))
    console.log("POST..end!", res);
    if(!res.includes(false)) {
      alert(' .\n     .\n       .');
      this.goBack();
    } else {
      alert('    .');
    }
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  async addImage(event) {
    event.persist()
    const {images} = this.state;
    if (event.target.files && event.target.files[0]) {
      for(let i=0; i<event.target.files.length; i++) {
        const reader = new FileReader();
        reader.onload = async (e) => {
          await images.push(e.target.result);
          this.setState({ images });
        };
        reader.readAsDataURL(event.target.files[i]);
      }
    }
  }

  render() {
    const {image, images} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}></div>
          <div className="title"> </div>
          <div className="register-btn on" onClick={this.getUploadUrl}></div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              <span>  .</span>
            </div>
            <div className="image-wrapper">
              {
                images.map((item, index) => (
                  <div className='image-addbox on' key={index} name='image' style={{backgroundImage: `url("${item}")`}} />
                ))
              }
              {
                images.length < 4 ? 
                  <div className='image-addbox' name='image' style={{backgroundImage: `url("${addPlusImage}")`}} onClick={() => onAttachImg()} /> : null
              }
              <input id='attachBtn' className="hide-button displaynone" name='image' type='file' accept="image/*" multiple onChange={this.addImage} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default ReviewContainer;
