import React from "react";
import { Link } from 'react-router-dom'
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

const HeaderComponent = ({type, dropdownOpen, toggle, filterData, optionList, params, searchTitle}) => (
  <Dropdown size="sm" isOpen={dropdownOpen} toggle={()=>toggle(type)}>
    <DropdownToggle caret>
      {searchTitle || optionList.optionTitle}
    </DropdownToggle>
    <DropdownMenu>
      {
        optionList.optionArr.map((item, index) => (
          <DropdownItem
            key={index}
            className={`${params === `${optionList.optionKey}=${item.link}` ? "on" : ""}`}
            onClick={()=>filterData(`${item.link ? `${optionList.optionKey}=${item.link}` : ''}`, item.name)}
          >
            {item.name}
          </DropdownItem>
        ))
      }
    </DropdownMenu>
  </Dropdown>
)

export default HeaderComponent
