import React from "react";

const Card = ({board}) => (
  <div className="board-card">
    <div className="board-tag">{board.category}</div>
    <div className="board-title">{board.title}</div>
    <div className="board-info">
      <div className="board-writer">{board.written_by || ""}</div>
      <div className="write-date">{board.created_at}</div>
    </div>
  </div>
)

export default Card
