import React, { Component } from 'react';

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class FeedbackContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      feedback: undefined,
      class_id: window.location.getParameterByName('id'),
    };
    this.postApi = this.postApi.bind(this);
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
  }

  async componentDidMount() {
    // const response = await api('get','/api/v1/community');
    // if(!response) return;
    // this.setState({boards: response.data.data});
  }

  async postApi() {
    const {class_id, feedback} = this.state;
    const { history } = this.props;
    const postData = {
      uploaded: feedback.toString()
    }
    const response = await api('post', `/api/v1/mentor/my_classes/${class_id}/feedback`, postData);
    if(!response) return;
    alert('    ');
    history.push(`/myclass/detail?id=${class_id}`);
  }


  clickOption(option) {
    this.setState({feedback: option })
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  render() {
    const {feedback} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">  </div>
          <div className={`register-btn ${feedback !== undefined ? "on" : ""}`} onClick={this.postApi}></div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
                 .
              <br />
                  
              <br />
                 .
            </div>
            <div className={`review-option ${feedback === true ? "on" : ""}`} onClick={()=>this.clickOption(true)}>  </div>
            <div className={`review-option ${feedback === false ? "on" : ""}`} onClick={()=>this.clickOption(false)}> </div>
          </div>
        </div>
      </div>
    )
  }
}

export default FeedbackContainer;
