import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Swiper from 'react-id-swiper';

import Header from "./HeaderContainer";
import DetailHeader from "../components/detail/header"
import Comment from "../components/comment"
import api from "../utils/api"
import '../styles/board.scss';
import backImg from '../images/global/btn-back@2x.png';

const swiperParams = {
  pagination: {
    el: '.swiper-pagination',
    type: 'bullets',
    clickable: true
  },
  spaceBetween: 30,
  shouldSwiperUpdate: true,
}

class BoardDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        images: ['https://www.google.com/url?sa=i&source=images&cd=&ved=2ahUKEwiJwLzyzuzfAhVHwbwKHachBm0QjRx6BAgBEAU&url=https%3A%2F%2Fm.post.naver.com%2Fviewer%2FpostView.nhn%3FvolumeNo%3D9498689%26memberNo%3D1976462&psig=AOvVaw1ciCufTJcwDpnmZzGBWigt&ust=1547532752571183', 'https://www.google.com/url?sa=i&source=images&cd=&ved=2ahUKEwiJwLzyzuzfAhVHwbwKHachBm0QjRx6BAgBEAU&url=https%3A%2F%2Fm.post.naver.com%2Fviewer%2FpostView.nhn%3FvolumeNo%3D9498689%26memberNo%3D1976462&psig=AOvVaw1ciCufTJcwDpnmZzGBWigt&ust=1547532752571183',],
        created_at: "",
        content: "",
        total_comments: 0,
        comments: []
      },
      comment: "",
      board_id: window.location.getParameterByName('id'),
      commentId: false,
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.submitComment = this.submitComment.bind(this);
    this.getApi = this.getApi.bind(this);
    this.goBack = this.goBack.bind(this);
    this.onWriteComment = this.onWriteComment.bind(this);
    this.onModifyComment = this.onModifyComment.bind(this);
    this.deleteComment = this.deleteComment.bind(this);
    this.deleteBoard = this.deleteBoard.bind(this);
  }

  componentDidMount() {
    this.getApi();
  }

  onModifyComment(id) {
    const {content} = this.state
    const selectedComment = content.comments.filter(item => item.id === id)[0];
    this.setState({ 
      commentId: id,
      comment: selectedComment.content,
    });
  }

  onWriteComment() {
    this.setState({ 
      commentId: false,
      comment: ''
    });
  }

  async getApi() {
    const {board_id} = this.state;  
    const response = await api('get',`/api/v1/community/${board_id}`);
    if(!response) return;
    this.setState({
      content: response.data.data,
      comment: "",
    });
  }

  getSwiper() {
    const {content} = this.state;
    return (
      <Swiper {...swiperParams}>
        {
          content.images.map((item, index) => 
            <div key={index+1} style={{backgroundImage: `url(${item.image})`, backgroundSize: 'contain'}} />
            // <img key={index} src={item.image} />
          )
        }
      </Swiper>
    )
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  async deleteBoard() {
    const {board_id} = this.state;
    const response = await api('delete',`/api/v1/community/${board_id}`);
    if(!response) return;
    alert('  .');
    this.goBack();
  }

  async submitComment() {
    const {comment, board_id, commentId} = this.state;
    const postData = {
      content: comment,
    }
    if(commentId) postData.comment_id = commentId;
    const response = await api(commentId ? 'put' : 'post', `/api/v1/community/${board_id}/comments`, postData);
    if(!response) return;
    swal(' ');
    this.getApi();
  }

  async deleteComment(id) {
    const {board_id} = this.state;
    const postData = {
      comment_id: id
    }
    const response = await api('delete', `/api/v1/community/${board_id}/comments`, postData);
    if(!response) return;
    swal(' ');
    this.getApi();
  }

  goBack(){
    const {history} = this.props;
    history.push('/board');
  }

  render() {
    const {content, comment, commentId, board_id} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title"></div>
          {
            content.written_by_me ? (
              <div style={{position: 'fixed', right: '16px', top: '16px',}}>
                <div
                  className="register-btn on"
                  onClick={this.deleteBoard}
                  style={{
                    float:'right',
                    position: 'initial',
                    top: 'initial',
                    right: 'initial',
                  }}
                >
                  
                </div>
                <Link
                  to={`/board/modify?id=${board_id}`}
                  style={{
                    float:'right',
                  }}
                >
                  <div
                    className="register-btn on"
                    style={{
                      position: 'initial',
                      top: 'initial',
                      right: 'initial',
                      marginRight: '16px',
                    }}
                  >
                    
                  </div>
                </Link>
              </div>
            ) : null
          }
        </div>
        <div className="board-container child-container">
          <div className="detail-container">
            <DetailHeader board={content} />
            {
              content.images && content.images.length ? (
                this.getSwiper()
              ) : null
            }
            <div className="detail-content">{content.content}</div>
            {
              content.raw_tags ? <div className="detail-tag">{`#${content.raw_tags.split(' ').join(' #')}`}</div> : null
            }
            <div className="comment-container">
              <div className="title">
                <span> </span>
                {content.comments.length}
              </div>
              <div className="comment-body">
                <table>
                  <colgroup>
                    <col width='56' />
                    <col width='auto' />
                  </colgroup>
                  <tbody>
                    {
                      content.comments.map((item, index) => (
                        <Comment
                          key={index}
                          comment={item}
                          onModifyComment={this.onModifyComment}
                          deleteComment={this.deleteComment}
                        />
                      ))
                    }
                  </tbody>
                </table>
              </div>
            </div>
            <div className="comment-inputbox">
              <input className={`comment-input ${commentId ? 'modify' : ''}`} type="text" placeholder="" name="comment" onChange={this.changeHandler} value={comment} />
              <button className="register-button" type="button" onClick={this.submitComment}>{commentId ? '' : ''}</button>
              {
                commentId ? (
                  <button className="cancel-button" type="button" onClick={this.onWriteComment}></button>   
                ) : null
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default BoardDetailContainer;

