/* eslint react/destructuring-assignment: 0 */

import React, { Component } from 'react';
import { connect } from "react-redux";
import axios from 'axios';

import { changeTitle, viewSidemenu } from "../actions/common";
import Header from "./HeaderContainer";
import api from "../utils/api";
import {classTime} from "../utils/static";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';
import addPlusImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = (index) => {
  document.getElementById(`attachBtn${index}`).click();
}

const receiptList = [
  {
    title: " ",
    name: "item_receipt",
    receipts: "item_receipt",
  },
  {
    title: " ",
    name: "transport_receipt",
    receipts: "transport_receipt",
  },
  {
    title: "",
    name: "accommodation_receipt",
    receipts: "accommodation_receipt",
  },
  {
    title: "",
    name: "food_receipt",
    receipts: "food_receipt",
  },
  {
    title: "(, )",
    name: "others_receipt",
    receipts: "others_receipt",
  },
]

class CalculateContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      classinfo: {
        school:"",
        inning: 0,
        start_time: false,
        end_time: false,
      },
      cost: {
        item_receipt: 0,
        transport_receipt: 0,
        accommodation_receipt: 0,
        food_receipt: 0,
        others_receipt: 0,
      },
      images: {
        item_receipt: false,
        transport_receipt: false,
        accommodation_receipt: false,
        food_receipt: false,
        others_receipt: false,
      },
      files: {
        item_receipt: false,
        transport_receipt: false,
        accommodation_receipt: false,
        food_receipt: false,
        others_receipt: false,
      },
      receipt_urls: {},
      class_id: window.location.getParameterByName('id'),
    };
    this.goBack = this.goBack.bind(this);
    this.addImage = this.addImage.bind(this);
    this.onBlurHandler = this.onBlurHandler.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.postApi = this.postApi.bind(this);
    this.removeImage = this.removeImage.bind(this);
    this.submitCost = this.submitCost.bind(this);
  }

  async componentDidMount() {
    const {class_id} = this.state;
    const response = await api('get',`/api/v1/mentor/my_classes/${class_id}/receipts`);
    this.setState({
      classinfo: response.data.data,
      receipt_urls: response.data.receipt_urls,
    });
  }

  onBlurHandler(e) {
    if(!e.target.value) this.setState({[e.target.name]: 0});
  }

  onChangeHandler(e) {
    const onlyNumber = /^(\d|[^-]-)+$/;
    const {cost} = this.state;
    let targetValue = e.target.value;
    if(targetValue) {
      if(onlyNumber.test(e.target.value)) {
        while (targetValue[0] === '0' && targetValue.length > 1) {
          targetValue = targetValue.slice(1, targetValue.length);
        }
        cost[e.target.name] = Number(targetValue);
        this.setState({cost});
      } else {
        alert(' ')
      }
    } else {
      cost[e.target.name]= targetValue;
      this.setState({cost});
    }
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  addImage(event) {
    event.persist();
    const {images, files} = this.state;
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();  
      reader.onload = (e) => {
        images[event.target.name] = e.target.result;
        [files[event.target.name]] = event.target.files;
        this.setState({ images, files });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  async postApi() {
    const {
      cost,
      images
    } = this.state;
    const costArr = Object.keys(cost).filter(item => cost[item])
    const imgArr = Object.keys(images).filter(item => images[item])
    const compareArr = costArr.filter(item => !imgArr.includes(item))
    
    if(!costArr.length) {
      alert('  ');
    } else if(!imgArr.length) {
      alert('  ');
    } else if(costArr.length !== imgArr.length || compareArr.length !== 0) {
      alert('  ');
    } else {
      console.log("imgArr", imgArr)
      this.submitImg(imgArr);
    }
  }

  async submitImg(imgArr) {
    const {files, receipt_urls} = this.state;
    console.log(receipt_urls, imgArr)
    const res = await Promise.all(imgArr.map(async item => {
      const formData = new FormData();
      formData.append('x-amz-date', receipt_urls[item].fields['x-amz-date']);
      formData.append('x-amz-credential', receipt_urls[item].fields['x-amz-credential']);
      formData.append('x-amz-algorithm', receipt_urls[item].fields['x-amz-algorithm']);
      formData.append('x-amz-signature', receipt_urls[item].fields['x-amz-signature']);
      formData.append('key', receipt_urls[item].fields.key);
      formData.append('policy', receipt_urls[item].fields.policy);
      formData.append('acl', receipt_urls[item].fields.acl);
      formData.append('file', files.item);

      const response = await axios({
        method: 'post',
        url: receipt_urls[item].url,
        data: formData,
        withCredentials: false,
      });
      console.log("POST..ing")
      console.log(response);
      return response.status === 204;
    }))
    console.log("POST..end!", res)
    const postResult = !res.includes(false);
    if(postResult) {
      this.submitCost();
    } else {
      alert('   ');
    }
  }

  async submitCost() {
    const {class_id, cost} = this.state;
    const postData = {
      item_needed: cost.item_receipt,
      transport_cost: cost.transport_receipt,
      accommodation_exp: cost.accommodation_receipt,
      food_exp: cost.food_receipt,
      others: cost.others_receipt,
    }

    const response = await api('post',`/api/v1/mentor/my_classes/${class_id}/receipts`, postData);
    if(!response) return;
    alert('  .');
    this.goBack();
  }

  removeImage(name) {
    const {images, files} = this.state;
    images[name]= false;
    files[name]= false;;
    this.setState({images, files});
  }

  render() {
    const {cost, classinfo, images} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}></div>
          <div className="title"> </div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              <p className="class-info">{classinfo.school}</p>
              <p className="class-info">{`#${classinfo.inning} (${classinfo.date}) ${classTime(classinfo.start_time)} - ${classTime(classinfo.end_time)}`}</p>
            </div>
            <div className="divider" />
            <div className="cost-container">
              {
                receiptList.map((item, index) => (
                  <div className="cost-wrapper" key={index}>
                    <div className="input-wrapper">
                      <span className="input-label">{item.title}</span>
                      <span className="input-unit"></span>
                      <input className="cost-input" type="number" onChange={this.onChangeHandler} onBlur={this.onBlurHandler} name={item.name} value={cost[item.name]} />
                    </div>
                    {
                      images[item.receipts] ? 
                        <div className="close-btn" onClick={() => this.removeImage(item.receipts)} style={{ position: 'absolute', right: '20px', marginTop: '-50px'}}>x</div> : null
                    }
                    <div className={`image-addbox ${images[item.receipts] ? "on" : ""}`} name={item.receipts} style={{backgroundImage: `url("${images[item.receipts] ? images[item.receipts] : addPlusImage}")`}} onClick={() => onAttachImg(index)} />
                    <input id={`attachBtn${index}`} className="hide-button displaynone" name={item.receipts} type='file' accept="image/*" onChange={this.addImage} />
                  </div>
                ))
              }
            </div>
          </div>
          <button className="report-button" type="submit" onClick={this.postApi}> </button>
        </div>
      </div>
    )
  }
}

export default CalculateContainer;
