import React from "react";
import { Link } from 'react-router-dom';

import api from "../../utils/api";

function convertDate(dates) {
  const month = new Date(dates).getMonth()+1;
  const date = new Date(dates).getDate();
  const day = new Date(dates).getDay();
  let convertDay = ''
  switch(day) {
    case 1:
      convertDay = '';
      break;
    case 2:
      convertDay = '';
      break;
    case 3:
      convertDay = '';
      break;
    case 4:
      convertDay = '';
      break;
    case 5:
      convertDay = '';
      break;
    case 6:
      convertDay = '';
      break;
    default:
      convertDay = '';
      break;
  }
  return `${month}/${date} (${convertDay})`;
}

const Card = ({item, toggleModal}) => (
  <tr>
    <td>
      <Link to={`/class/detail?id=${item.uuid}&applied=${item.applied ? '1' : '0'}`}>
        {item.region}
      </Link>
    </td>
    <td>
      <Link to={`/class/detail?id=${item.uuid}&applied=${item.applied ? '1' : '0'}`}>
        { item.school.includes('') ? item.school.split('')[0] : item.school.split('')[0]}
      </Link>
    </td>
    <td>
      <Link to={`/class/detail?id=${item.uuid}&applied=${item.applied ? '1' : '0'}`}>
        {convertDate(item.date)}
      </Link>
    </td>
    <td>
      <Link to={`/class/detail?id=${item.uuid}&applied=${item.applied ? '1' : '0'}`}>
        {item.program || ''}
      </Link>
    </td>
    <td>
      <div
        className={`apply-button  ${item.applied === true ? 'complete' : 'on'}`}
        onClick={() => toggleModal(`${item.applied === true ? 'cancel' : 'request'}`, item.uuid)}
        // onClick={item.applied === true ? () => cancelClass(item.uuid) : () => requestClass(item.uuid)}
      >
        {item.applied ? '' : ''}
      </div>
    </td>
  </tr>
)

export default Card

